// Contain colors, fonts, sizes, etc.
.body-reg{
    height: 100vh;
    background: rgb(42, 47, 51) !important; //#1C658C
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}
.card{
    overflow: hidden;
    border: 0;
    border-radius: 20px;
    box-shadow: 0 0.5rem 1rem 0 rgba(0,0,0,0.1);
}

.img-left{
    width: 45%;
    background: url('../img/img_login1.jpg') center;
    background-size: cover;

}

.img-left-2{
    width: 45%;
    background: url('../img/img_register.jpg') center;
    background-size: cover;

}

.img-overview{
    width: 45%;
    background: url('../img/img_overview.jpg') center;
    background-size: cover;

}

.card-body{
    padding: 2rem;
    background: rgb(223, 222, 222);
}
.title{
    margin-bottom: 2rem;
}
.form-input{
    position: relative;
}
.form-input input{
    width: 100%;
    height: 45px;
    padding-left: 40px;
    margin-bottom: 20px;
    box-sizing: border-box;
    box-shadow: none;
    border: 1px solid #00000020;
    border-radius: 50px;
    outline: none;
    background: transparent; //transparent
}
.form-input span{
    position: absolute;
    top: 10px;
    padding-left: 15px;
    color: #398AB9;
}
.form-input input::placeholder{
    color: black;
    padding-left: 0px;
}
.form-input input:focus, .form-input input:valid{
    border: 2px solid #398AB9;
}
.form-input input:focus::placeholder{
    color: #1C658C;
}
.custom-checkbox .custom-control-input:checked ~ .custom-control-label::before{
    background-color: #398AB9;
    border: 0px;
}
.form-box button[type="submit"]{
    margin-top: 10px;
    border: none;
    cursor: pointer;
    border-radius: 50px;
    background: #398AB9;
    color: #fff;
    font-size: 90%;
    font-weight: bold;
    letter-spacing: .1rem;
    transition: 0.5s;
    padding: 12px;
}
.form-box button[type="submit"]:hover{
    background: #398AB9;
}
.forget-link, .register-link{
    color: #398AB9;
    font-weight: bold;
}
.forget-link:hover, .register-link:hover{
    color: #398AB9;
    text-decoration: none;   
}
.form-box .btn-social{
    color: white;
    border: 0;
    font-weight: bold;
}
.form-box .btn-facebook{
    background: #4866a8;
}
.form-box .btn-google{
    background: #da3f34;
}
.form-box .btn-twitter{
    background: #33ccff;
}
.form-box .btn-facebook:hover{
    background: #3d578f;
}
.form-box .btn-google:hover{
    background: #bf3b31;
}
.form-box .btn-twitter:hover{
    background: #2eb7e5;
}

// Format dropdown navbar home
.dropdown-menu-center {
    left: 50% !important;
    right: auto !important;
    text-align: center !important;
    transform: translate(-50%, 0) !important;
  }

.masthead .masthead-avatar {
    width: 15rem;
  } 

.masthead {
    padding-top: calc(6rem); //
    padding-bottom: 6rem;
}

.masthead .masthead-heading {
    font-size: 2.75rem; 
    line-height: 2.75rem;
}

.masthead .masthead-subheading {
    font-size: 1.25rem;
}

  
@media (min-width: 992px) {
.masthead {
    padding-top: calc(6rem);
    padding-bottom: 6rem;
}
.masthead .masthead-heading {
    font-size: 4rem;
    line-height: 3.5rem;
}
.masthead .masthead-subheading {
    font-size: 1.5rem;
}
}
