// Dropdown Menu Layout App

.sub-menu-wrap{
    position: absolute;
    top: 100%;
    right: 1%;
    width: 250px;
    max-height: 0px;
    overflow: hidden;
    transition: max-height 0.5s;
}

.open-menu{
    max-height: 400px;
}

.sub-menu{
    background: #fff;
    padding: 20px;
    margin: 10px;
}

.user-info{
    display: flex;
    align-items: center;
}

.user-info h3{
    font-weight: 400px;
    font-size: 15px;
    color: black;

}

.user-info img{
    width: 60px;
    border-radius: 50%;
    margin: 15px;
}

.sub-menu hr{
    border: 0%;
    height: 1px;
    width: 100%;
    background: #ccc;
    margin: 0px 0 0px;
}

.sub-menu-link{
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #525252;
    margin: 12px 0;
}

.sub-menu-link span{
    font-weight: 600;
    font-size: 20px;
    transition: transform 0.5s;
}

.sub-menu-link img{
    width: 30px;
    height: 30px;
    margin-right: 15px;
}

.sub-menu-link p{
    width: 100%;
}

.sub-menu-link:hover span{
    transform: translateX(5px);
}

.sub-menu-link:hover p{
    font-weight: 600;
}
.user-pic{
    width: 40px;
    margin-left: 10%;
    cursor: pointer;
}

// Sidebar

.btn-toggle {
    display: inline-flex;
    align-items: center;
    padding: .25rem .5rem;
    font-weight: 600;
    color: rgba(0, 0, 0, .65);
    background-color: transparent;
    border: 0;
  }
  .btn-toggle:hover,
  .btn-toggle:focus {
    color: rgba(0, 0, 0, .85);
    background-color: #d2f4ea;
  }
  
  .btn-toggle::before {
    width: 1.25em;
    line-height: 0;
    transition: transform .35s ease;
    transform-origin: .5em 50%;
  }
  
  .btn-toggle[aria-expanded="true"] {
    color: rgba(0, 0, 0, .85);
  }
  .btn-toggle[aria-expanded="true"]::before {
    transform: rotate(90deg);
  }
  
  .btn-toggle-nav a {
    display: inline-flex;
    padding: .1875rem .5rem;
    margin-top: .125rem;
    margin-left: 1.25rem;
    text-decoration: none;
  }
  .btn-toggle-nav a:hover,
  .btn-toggle-nav a:focus {
    background-color: #d2f4ea;
  }